<template>
    <div class="main">
        <div class="regionalManagement">
            <div class="searchA">
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn(1,null)">新增</el-button>
                </div>
            </div>
            <el-table :data="tableData" border style="width: 100%" size="medium" :max-height="tableHeight" v-loading="isLoading" row-key="id" :tree-props="{children: 'children'}" :default-expand-all='true'>
                <el-table-column prop="region_name" label="区县">
                </el-table-column>
                <el-table-column label="操作" fixed="right" width="200">
                    <template slot-scope="scope">
                        <el-button @click="addBtn(2,scope.row)" v-if="scope.row.parent_id==0" type="text" size="small" style="color:#67c23a">新增下级</el-button>
                        <el-button @click="editBtn(scope.row)" type="text" size="mini">编辑</el-button>
                        <el-button @click="delBtn(scope.row.id)" type="text" size="mini" style="color: #ff2b2b">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 新增编辑弹窗 -->
            <el-dialog :title="title" v-dialogDrag :visible.sync="dialogVisible" width="600px" v-if="dialogVisible" :close-on-click-modal="false">
                <div>
                    <el-form :model="formData" ref="formData" :rules="rule" class="demo-ruleForm">
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="region_name" label="区域名称">
                                        <el-input v-model="formData.region_name" placeholder="请输入区域名称" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>

                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="region_code" label="区域编码">
                                        <el-input v-model="formData.region_code" placeholder="请输入区域编码(12位)" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div class="dialogInput">
                            <div class="padd">
                                <div>
                                    <el-form-item prop="simple_code" label="区域简码">
                                        <el-input v-model="formData.simple_code" placeholder="请输入区域编码(6位)" size="small"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div style="text-align: center">
                            <el-button type="primary" class="btn" @click="submitForm('formData')">确 定</el-button>
                        </div>
                    </el-form>
                </div>
            </el-dialog>

        </div>
    </div>
</template>
<script>
import {
    hashApi,
    regionalManagementListApi,
    regionalManagementAddApi,
    regionalManagementEditApi,
    regionalManagementDelApi
} from "@/api/index";
export default {
    data() {
        return {
            tableData: [],
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                region_name: '',
                region_code: '',
                simple_code: '',
                disabledS: true,
            },
            title: '',
            hash: "",
            dialogVisible: false,
            type: '',
            rule: {
                region_name: [
                    { required: true, message: '请输入区域名称', trigger: 'blur' },
                ],
                region_code: [
                    { required: true, message: '请输入区域编码', trigger: 'blur' },
                ],
                simple_code: [
                    { required: true, message: '请输入区域简码', trigger: 'blur' },
                ],
            },
        }
    },
    mounted() {
        this.getHashData();
        this.getList();
    },
    methods: {
        //获取列表
        async getList() {
            this.isLoading = true;
            const res = await this.axios.post(regionalManagementListApi, {
                format: 1,
            })
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
                this.defaulted = res.data.data.defaulted;//删除权限
                this.isLoading = false;

            } else {
                this.isLoading = false;
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
        //重置对话框表单
        resetForm() {
            this.formData = {
                region_name: '',
                region_code: '',
                simple_code: '',
                disabledS: false,
            };
        },
        // 点击新增
        addBtn(type, row) {
            this.title = '新增';
            this.dialogVisible = true;
            this.type = type;
            if (row) {
                this.parent_id = row.id;
            }
            this.resetForm();
            this.getHashData();
        },
        // 点击编辑
        editBtn(e) {
            this.title = '编辑';
            this.dialogVisible = true;
            this.resetForm();
            this.formData.region_name = e.region_name;
            this.formData.region_code = e.region_code;
            this.formData.simple_code = e.simple_code;
            this.formData.disabledS = (e.disabled == 0 ? true : false);
            this.listId = e.id;
            this.getHashData();
            // this.$nextTick(() => {
            //     this.$refs['formData'].clearValidate();
            // });
        },
        // 新增编辑提交
        submitForm(formName) {
            this.$refs[formName].validate(async (valid) => {
                if (valid) {
                    let datas = {
                        region_name: this.formData.region_name,
                        region_code: this.formData.region_code,
                        simple_code: this.formData.simple_code,
                        disabled: this.formData.disabledS == true ? 0 : 1,
                        hash: this.hash,

                    };
                    if (this.type == 1) {
                        datas.parent_id = 0;
                    } else if (this.type == 2) {
                        datas.parent_id = this.parent_id;
                    }
                    if (this.title == '新增') {
                        var urls = regionalManagementAddApi;
                    } else if (this.title == '编辑') {
                        var urls = regionalManagementEditApi;
                        datas.id = this.listId;
                    }

                    const res = await this.axios.post(urls, datas);
                    if (res.data.code == 1) {
                        this.dialogVisible = false;
                        this.$nextTick(() => {
                            this.getList();
                        });
                        this.getHashData();
                    } else {
                        this.getHashData();
                        this.$message({
                            showClose: true,
                            message: res.data.msg,
                            type: 'error',
                        });
                    }
                } else {
                    return false;
                }
            });
        },

        //删除
        async delBtn(e) {
            this.$confirm('是否删除该条数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(async () => {
                const params = {
                    id: e,
                    hash: this.hash,
                };
                const { data } = await this.axios.post(regionalManagementDelApi, params);
                if (data.code == 1) {
                    if ((this.total - 1) % this.pageSize === 0) {
                        this.pageNum = this.pageNum - 1
                    }
                    this.getList();
                    this.getHashData();
                    this.$message({ showClose: true, message: '删除成功', type: 'success', });
                } else {
                    this.getHashData();
                    this.$message({ showClose: true, message: data.msg, type: 'error', });
                }
            })
                .catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除',
                    });
                });
        },

        //获取hash
        async getHashData() {
            const datas = {
                page_hash: 'regionalManagement'
            };
            const res = await this.axios.post(hashApi, datas)
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: "error"
                });
            }
        },
    },
}
</script>

<style lang="scss" scoped>
/deep/.regionalManagement .el-form-item__label {
    padding: 0;
}
.regionalManagement {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
            //       .el-button{
            //   border: solid 1px #2f39ff;
            //   color: #2f39ff;
            // }
        }
    }

    .dialogInput {
        display: flex;
        // margin-bottom: 10px;
        .tree_main {
            border: 1px solid #dcdfe6;
            padding: 15px;
            max-height: 200px;
            overflow-y: auto;
        }
        .padd {
            width: 100%;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }

        .flexBox {
            display: flex;
            justify-content: space-between;
            .flex_1 {
                flex: 1;
                &:nth-child(1) {
                    margin-right: 30px;
                }
                .el-date-editor {
                    width: 100%;
                }
            }
        }
    }
    .btn {
        width: 250px;
        // margin-top: 20px;
    }
}
</style>


